.alerta {
    padding: .5rem;
    text-align: center;
    color: white;
    font-weight: 700;
    text-transform: uppercase;
    margin: 1rem 0;
    
    &.error {
        background-color: red;
    }
    &.exito {
        background-color: #71B100;
    }
}

table.propiedades{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    thead{
        background-color: #71B100;

        th {
            color: white;
            padding: 2rem;
        }
    }
    .imagen-tabla {
        width: 10rem;
    }
}

.imagen-small {
    width: 20rem;
}